<template>
  <div class="index" @click="handleDownload">
    <div class="index-top">
      <div class="index-top-arrow">
        <img :src="arrow3x" alt="arrow">
      </div>
      <div class="index-top-btn">
        <img :src="browser.isIOS ? appStore3x : yyb3x" alt="download btn">
      </div>
    </div>
    <div class="index-middle">
      <div class="index-middle-logo">
        <img :src="logo3x" alt="pop im logo">
      </div>
      <div class="index-middle-text">
        <img :src="popIm3x" alt="pop im text">
      </div>
    </div>
  </div>
</template>

<script>
import browser from '@/utils/browser';
import arrow3x from '../assets/arrow@3x.png';
import appStore3x from '../assets/app_store@3x.png';
import yyb3x from '../assets/yyb@3x.png';
import logo3x from '../assets/logo@3x.png';
import popIm3x from '../assets/pop_im@3x.png';
import UrlSchema from '@sundaymorning/urlschema';
export default {
  name: 'index',

  data () {
    return {
      arrow3x,
      appStore3x,
      yyb3x,
      logo3x,
      popIm3x,
      browser,
      config: {
        appstore: 'https://itunes.apple.com/cn/app/pop-im/id1381210087?ls=1&mt=8',
        yyb: 'https://a.app.qq.com/o/simple.jsp?pkgname=com.sundayfun.daycam'
      }
    };
  },

  methods: {
    handleDownload () {
      if (browser.isWx) {
        new UrlSchema(this.config).open();
      } else {
        window.location.href = new UrlSchema(this.config).getDownloadUrl();
      }
    }
  }
};
</script>

