const UA = navigator.userAgent || '';
const isIOS = !!UA.match(/iPhone|iPod/i);
const isAndroid = !!UA.match(/Android/i);
const isMac = !!UA.match(/Mac/i);
const isWx = !!UA.match(/micromessenger/i);
const isChrome = !!UA.match(/CriOS\/([\d.]+)/);
const isSafari = !!UA.match(/Safari\/([\d.]+)/) && !isChrome;
const isMobile = !!UA.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);

export default {
  isWx,
  isIOS,
  isAndroid,
  isMac,
  isSafari,
  isChrome,
  isMobile
};
